import { out } from '@elog/shared'
import path from 'path'
import {
  htmlAdapter,
  markdownAdapter,
  matterMarkdownAdapter,
  wikiAdapter,
} from '@elog/plugin-adapter'
import { AdapterConfig, AdapterFunction } from '../types'
import { FormatEnum, formatList } from '../const'

/**
 * ÊñáÊ°£Â§ÑÁêÜÈÄÇÈÖçÂô®
 */
export class AdapterClient {
  config: AdapterConfig
  ctx: AdapterFunction

  constructor(config: AdapterConfig) {
    this.config = config
    this.ctx = this.initAdapter()
  }

  /**
   * ÂàùÂßãÂåñÈÄÇÈÖçÂô®
   * @private
   */
  private initAdapter() {
    if (this.config.formatExt) {
      out.warning('Ê≥®ÊÑè', 'Ê≠£Âú®Âä†ËΩΩÊñáÊ°£Â§ÑÁêÜÊãìÂ±ïÁÇπÔºåËØ∑ÈÅµÂæ™ÊñáÊ°£Â§ÑÁêÜÊãìÂ±ïÁÇπÊ≥®ÂÖ•ËßÑËåÉ')
      try {
        // Âä†ËΩΩÊãìÂ±ïÁÇπ
        // Â¶ÇÊûúÊåáÂÆö‰∫ÜsecretÊãìÂ±ïÁÇπÔºåÈÇ£‰πàÊãìÂ±ïÁÇπËøîÂõûÁöÑË¥¶Âè∑ÂØÜÁ†Å‰ø°ÊÅØÔºåÂ∞Ü‰ºöË¶ÜÁõñelog-config.json‰∏≠ÁöÑimage‰ø°ÊÅØ
        const formatExtPath = path.resolve(process.cwd(), this.config.formatExt)
        // ÊãìÂ±ïÁÇπÈúÄË¶ÅÊö¥Èú≤formatÊñπÊ≥ï
        const { format } = require(formatExtPath)
        return format
      } catch (e: any) {
        out.err(e.message)
        out.err('ÊñáÊ°£Â§ÑÁêÜÊãìÂ±ïÁÇπÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÔºÅ')
        process.exit(1)
      }
    } else {
      switch (this.config.format) {
        case FormatEnum.MARKDOWN:
          return markdownAdapter
        case FormatEnum.MATTER_MARKDOWN:
          return matterMarkdownAdapter
        case FormatEnum.WIKI:
          return wikiAdapter
        case FormatEnum.HTML:
          return htmlAdapter
        default:
          out.warning(`ÁõÆÂâçÂè™ÊîØÊåÅÂ∞ÜÊñáÊ°£ËΩ¨Êç¢‰∏∫${formatList.toString()}ÔºåÂ∞ÜÈªòËÆ§‰ª•markdownÂΩ¢ÂºèËΩ¨Êç¢`)
          return markdownAdapter
      }
    }
  }

  /**
   * Ëé∑ÂèñÊñáÊ°£Â§ÑÁêÜÂô®
   */
  public getAdapter() {
    return this.ctx
  }
}
